.slider {
  position: relative;
  max-width: 675px;
  max-height: 675px;
  width: 41vw;
  height: 41vw;
  border-radius: 30px 0;

  background-image: url(../images/slider/slide-img-1.png);
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: 860px) {
    display: none;
  }

  &__arrow-left {

    &::before {
      position: absolute;
      
      top: 85%;
      left: 7%;
  
      content: "";
      width: 32px;
      height: 32px;
      background-image: url(../images/slider/arrow-left.svg);
      background-size: contain;

      transition: transform $transition-duration ease;
  
      @media (max-width: 980px) {
        left: 5%;
      }
  
      @media (max-width: 886px) {
        top: 87%;
        left: 50%;
        transform: translateX(-100%);
      }
    }

    &:hover::before {
      transform: scale(1.2);
    }
  }

  &__arrow-right {

    &::after {
      position: absolute;
      
      top: 85%;
      left: 15%;
  
      content: "";
      width: 32px;
      height: 32px;
      background-image: url(../images/slider/arrow-right.svg);
      background-size: contain;

      transition: transform $transition-duration ease;
  
      @media (max-width: 886px) {
        top: 87%;
        left: 50%;
        transform: translateX(10%);
      }
    }

    &:hover::after {
      transform: scale(1.2);
    }
  }

  &__heading {
    @include nav-font;

    position: absolute;
    top: 83%;
    left: 25%;

    @media (max-width: 980px) {
      top: 87%;
      left: 50%;
      transform: translateX(-50%);
    }

    @media (max-width: 886px) {
      top: 80%;
      left: 50%;
      transform: translateX(-45%);
    }
  }

  &__paragraph {
    @include decsription;

    position: absolute;
    top: 87%;
    left: 25%;

    max-width: 264px;

    @media (max-width: 980px) {
      display: none;
    }
  }
}
